}
.page-content a {
    color: #ffffff;
}
.page-content.white-bg a.logo {
    color: #363636;
}
.page-content a.navigation__arrow svg path {
    transition: all .3s ease-in;
}
.page-content a.navigation__arrow:hover svg path {
    fill: #ffffffb5;
    transition: all .3s ease-in;
}
.page-content.white-bg a.navigation__arrow svg path {
