}
.preloader{
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100vw;
    height: 50vh;
    top: 0px;
    z-index: 999;
    background-color: #363636;
    flex-direction: column;
    transition: top .8s ease-in-out .3s;
}
.preloader.hide {
    top: -50vh;
    transition: top .8s ease-in-out .3s;
}
.preloader-bottom {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100vw;
    height: 50vh;
    bottom: 0px;
    z-index: 999;
    background-color: #363636;
    flex-direction: column;
    transition: bottom .8s ease-in-out .3s;
}
.preloader-bottom.hide {
    bottom: -50vh;
    transition: bottom .8s ease-in-out .3s;
}
.percent{
    color: #ffffff;
    font-size: 3.2rem;
    opacity: 1;
    transition: opacity .3s ease-in-out;
}
.bar{
    width: 30vw;
    margin-top: 30px;
    opacity: 1;
    transition: width .3s ease-in-out, opacity .1s .3s;
}
.preloader.hide .percent {
    opacity: 0;
    transition: opacity .3s ease-in-out;
}
.preloader.hide .bar {
    width: 100vw;
    opacity: 0;
    transition: width .3s ease-in-out, opacity .1s ease-in-out .3s;
}
.barconfrm{
    width: 1%;
    padding: 2px 0px;
    background-color: #f94444;

}
.loading {
    position: relative;
    display: inline-block;
    opacity: 0;
    transform: rotateX(-90deg);
    color: #ffffff;
    font-size: 1.8rem;
    font-weight: 500;
}
.loading__wrapper {
    position: absolute;
    top: -30px;
    transition: opacity .3s ease-in-out;
}
.preloader-bottom.hide .loading__wrapper {
    opacity: 0;
    transition: opacity .3s ease-in-out;
}
