.parallax {
    /* background-image: url(img/main-bg__mylo.png); */
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0px;
    left: 0px;
    background-position: left 2%;
    background-repeat: no-repeat;
    background-size: 100%;
    z-index: -1;
    transition: background ease-in-out 1.2s .1s;
}
.svg__wrapper {
    position: fixed;
    top: -25vh;
    width: 100vw;
    height: 100vh;
    z-index: -1;
}
.second-screen .parallax {
    background-position: center 44%;
    transition: background ease-in-out 1.2s .1s;
    background-size: 120%;
}
.page-content .parallax {
    background-size: 120%;
}
.page-content.second-screen .parallax {
