.navigation {
    position: fixed;
    left: 2.45%;
    top: 12vh;
    z-index: 2;
    /* overflow: hidden; */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 88vh;
}
.navigation .navigation__arrow {
    transform: translateX(-110%);
    transition: all .3s ease-in-out;
    cursor: pointer;
}
.page-content .navigation .navigation__arrow {
    transform: translateX(0vw);
    transition: all .3s ease-in-out;
}
.page-content .navigation .navigation__arrow:hover {
    transform: translateX(-0.5vw);
    transition: all .3s ease-in-out;
}
.navigation__count {
    position: fixed;
    top: 0px;
    bottom: 0px;
    margin: auto 0px;
    max-height: 11vh;
    left: 2.45%;
    transition: all .5s ease-in-out;
    z-index: 106;
}
.animate .navigation__count {
    left: -130%;
    transition: all .5s ease-in-out;
}
.page-content .navigation__count {
    left: -130%;
    transition: all .5s ease-in-out;
}
.navigation__line {
    width: 0px;
    height: 3px;
    background: #ffffff;
    margin-bottom: 3.241vh;
    transition: width ease-in-out .5s .5s;
    z-index: 3;
    cursor: pointer;
}
.navigation__line:last-child {
    margin-bottom: 0px;
}
.navigation__line {
    width: 0px;
    transition: width ease-in-out .5s .5s;
}
.black.second-screen .navigation__line.about {
    width: 2.344vw;
    animation-name: activeLine;
    animation-duration: 1.2s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: 1;
    animation-play-state: running;
    animation-delay: .5s;
}
.black.third-screen .navigation__line.works {
    width: 2.344vw;
    transition: width ease-in-out .5s .5s;
}
.black.fourth-screen .navigation__line.contacts {
    width: 2.344vw;
    transition: width ease-in-out .5s .5s;
}
@keyframes activeLine{
    0%{
        width: 0px;
    }
    25%{
        width: 1.563vw;
    }
    100%{
        width: 2.344vw;
    }
}
.black .navigation__line {
    width: 1.563vw;
}
.black .navigation__line:first-child {
    transition: width ease-in-out .2s .5s;
}
.black .navigation__line:nth-child(3) {
    transition: width ease-in-out .4s .5s;
}
.black .navigation__line:last-child {
    transition: width ease-in-out .5s .5s;
}

.navigation__scrolldown {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    transform: translateY(0px);
    transition: transform ease-in-out 1s;
    cursor: pointer;
}
.page-content .navigation__scrolldown  {
    transform: translateY(120%);
}
.navigation__scrolldown:hover {
    transform: translateY(20px);
    transition: transform ease-in-out 1s;
}
.navigation__scrolldown-text {
