.white-bg .navigation__scrolldown.animate {
    transform: translateY(200px);
    transition: transform ease-in-out 1s;
}
.fourth-screen  .navigation__scrolldown {
    transform: translateY(120%);
    transition: transform ease-in-out 1s;
}
.first-screen  .navigation__scrolldown {
    transform: translateY(120%);
    transition: transform ease-in-out 1s;
}

