/**/
.main-screen {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    /* font-family: 'Neuton'; */
    /* font-family: 'Cormorant SC', serif; */
    flex-direction: column;
    position: relative;
}
.main-screen__big-text {
    letter-spacing: 0.8em;
    font-size: 9rem;
    text-align: right;
    margin-right: -0.8em;
    line-height: 7.6rem;
    font-weight: 700;
    position: relative;
    /* transform: scale(0.9); */
    color: transparent;
    opacity: 0;
    transition: opacity .3s ease-in-out;
}
.main-screen__big-text .block {
    visibility: visible;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.main-screen__big-text .visible {
    color: transparent;
    -webkit-text-stroke: 3px #363636;
    font-size: 8.97rem;
    z-index: 102;
    position: static;
    transform: none;
}
.animate .main-screen__big-text .block span {
    color: #363636;
}
.animate .main-screen__big-text .block span:hover {
    color: transparent!important;
    transition: color .1s ease-in-out 0s!important;
}


.main-screen__big-text .block span.one {
    transition: color .3s ease-in-out .6s;
}
.main-screen__big-text .block span.two {
    transition: color .3s ease-in-out .7s;
}
.main-screen__big-text .block span.three {
    transition: color .3s ease-in-out .8s;
}
.main-screen__big-text .block span.four {
    transition: color .3s ease-in-out .9s;
}
.main-screen__big-text .block span.five {
    transition: color .3s ease-in-out 1s;
}
.main-screen__big-text .block span.six {
    transition: color .3s ease-in-out 1.1s;
}
.main-screen__big-text .block span.seven {
    transition: color .3s ease-in-out 1.3s;
}
.main-screen__big-text .block span.eight {
    transition: color .3s ease-in-out 1.4s;
}
.main-screen__big-text .block span.nine {
    transition: color .3s ease-in-out 1.5s;
}


.animate .main-screen__big-text .block span.one {
    transition: color .3s ease-in-out;
}
.animate .main-screen__big-text .block span.two {
    transition: color .3s ease-in-out .1s;
}
.animate .main-screen__big-text .block span.three {
    transition: color .3s ease-in-out .2s;
}
.animate .main-screen__big-text .block span.four {
    transition: color .3s ease-in-out .3s;
}
.animate .main-screen__big-text .block span.five {
    transition: color .3s ease-in-out .4s;
}
.animate .main-screen__big-text .block span.six {
    transition: color .3s ease-in-out .5s;
}
.animate .main-screen__big-text .block span.seven {
    transition: color .3s ease-in-out .6s;
}
.animate .main-screen__big-text .block span.eight {
    transition: color .3s ease-in-out .7s;
}
.animate .main-screen__big-text .block span.nine {
    transition: color .3s ease-in-out .8s;
}


.animate .main-screen__big-text {
    transform: scale(1.0);
    opacity: 1;
    transition: opacity .3s ease-in-out;
}

.main-wrapper .main-screen__tex .main-screen__big-text .block.effect span {
    transition: color .1s ease-in-out 0s!important;
}

.main-screen__text {
    font-size: 5.15rem;
    font-weight: 400;
    color: transparent;
    -webkit-text-stroke: 1px #363636;
    opacity: 0;
    text-align: center;
    transition: all .5s ease-in-out;
}
.animate .main-screen__text {
    opacity: 1;
    color: #363636;
    transition: all .5s ease-in-out;
}
.main-screen__text span {
