.logo {
    font-size: 2.5rem;
    font-weight: 500;
    margin-right: 0.885vw;
    cursor: pointer;
    transition: all .3s ease-in;
    border-bottom: 2px solid rgba(0, 0, 0, 0);
    position: relative;
    display: block;
    -webkit-text-stroke: 2px transparent;
    z-index: 101;
}
.logo:hover {
    transition: all ease-in .3s;
    color: #ffffffb5;
}
.black .logo:hover {
    transition: all ease-in .3s;
    color: #ffffffb5;
}
.logo:hover .visible {
    letter-spacing: 2px;
    transition: all .4s ease;
    transform: translate(-50%, -50%);
}
.black .logo:hover .visible {
    color: #ffffffb5;
}
.white-bg .logo:hover .visible {
    color: #363636a1;
}
.logo::after {
    border-bottom: 2px solid #f94444;
    color: #363636a1;
