/* stack */

.stack {
    padding: 0px 8vw 8.5vw 8vw;
    transition: all .3s ease;
    background-color: #34353a;
    color: #fff;
    display: grid;
    gap: 3vw;
    grid-template-columns: repeat(auto-fit, minmax(252px, 1fr));
}
/* .stack__row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 5vw;
} */
.stack__item {
    display: flex;
    align-items: center;
    background: #3d3e42;
    flex-direction: column;
    padding: 30px 60px;
    border-radius: 10%;
    min-height: 9vw;
    justify-content: space-between;
    transition: all .3s ease-in-out;
}
.stack__item:hover {
    background: #f9444452;
}
.stack__item-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.stack__item-text__title {
    font-weight: 700;
    font-size: 2.2rem;
    letter-spacing: .3rem;
    margin-top: 2rem;
    font-family: "GT Cinetype Regular";
}
.exp {
    color: #ffffffb5;
    font-weight: 300;
    font-size: 1.6rem;
    margin-top: 10px;
}



/* page-footer */
footer.footer {
    height: 30vh;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    position: relative;
    overflow: hidden;
    background: rgba(255, 255, 255, 1);
    color: #363636;
}
.footer__block {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
}
.footer-bigText {
    font-weight: 900;
    font-style: italic;
    font-size: 9rem;
    width: 33.333vw;
    text-align: center;
    z-index: 200;
}
.footer-bigText:last-child {
    opacity: 0;
    z-index: -1;
}
.footer-smallText {
    font-weight: 500;
    font-size: 1.8rem;
    color: #363636;
    text-align: center;
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 33.333vw;
}
.footer-smallText a.text {
    color: #363636;
    text-align: center;
    margin-right: 15px;
    margin-top: 3px;
    display: flex;
    position: relative;
}
.footer-social-icons {
    justify-content: center;
    margin-top: 15px;
}
.background-circle {
    position: absolute;
    display: block;
    top: 4%;
    right: 30%;
}
.background-ellipse {
    position: absolute;
    display: block;
    top: 19%;
    left: -3%;
    z-index: 1;
}
.background-ellipse__second {
    position: absolute;
    display: block;
    top: -180%;
    right: 0%;
}


