    top: -250%;
    width: 0.05vw;
    transition: opacity ease-in-out .3s, left linear 0s, width linear .6s 0s;
}
.project__botton.third-screen__button::after {
    height: 550%;
    transform: rotate(22deg);
    top: -250%;
    width: 0.05vw;
    transition: opacity ease-in-out .3s, right linear 0s, width linear .6s 0s;
}
.project__botton.third-screen__button:hover::before {
    transition: opacity ease-in-out .2s, left linear .2s, width linear .2s .2s;
    left: 50%;
    width: 70%;
}
.project__botton.third-screen__button:hover::after {
    transition: opacity ease-in-out .2s, right linear .2s, width linear .2s .2s;
    right: 50%;
    width: 70%;
}
